import {
  Card,
  CardHeader,
  CardBody,
  CardFooter,
  Input,
  Button,
  Typography,
} from "@material-tailwind/react";
import { login } from "session/Session.ts";
import Sidebar from "layout/Sidebar";
import routes from "routes/SidebarRoutes";
import { ArrowLongRightIcon } from "@heroicons/react/24/solid";

export default function LoginPage() {

  const [credentials, setCredentials] = useState<ICredentials>({username: "", password: ""})

  const navigate = useNavigate();

  async function loginForm() {
    try {
      if(!credentials.username || credentials.username === "" ||
      !credentials.password || credentials.password === ""
      ) {
          Swal.fire({icon: 'error', title: 'Eroare', text: 'Trebuie completate username si password!'})
          return;
      }
      
      await login(credentials.username, credentials.password);

      navigate("/dashboard/home");

    } catch (err) {
      Swal.fire({icon: 'error', title: 'Eroare', text: err});
    }
  }

  function handleKeyPress(event) {
    if (event.key === "Enter") {
      loginForm();
    }
  }

  return(
    <>
      <div className="absolute inset-0 z-0 h-full w-full bg-gradient-to-r from-indigo-500" />
      <div className="container mx-auto p-4">
        <Card className="absolute top-2/4 left-2/4 w-full max-w-[24rem] -translate-y-2/4 -translate-x-2/4">
          <CardHeader
            variant="gradient"
            color="blue"
            className="mb-4 grid h-28 place-items-center"
          >

            
                <div className="flex gap-4">
                  <Avatar src="/logo.png" alt="avatar" variant="rounded" />
                  <Typography className="flex items-center gap-2" variant="h3" color="black"> Electra </Typography>
                </div>
            
            
          </CardHeader>
          <CardBody className="flex flex-col gap-4">
            <Input
              type="email"
              label="Username"
              size="lg"
              onChange={(e) => {setCredentials({... credentials, username: e.target.value})}}
            />
            <Input
              type="password"
              label="Password"
              size="lg"
              onChange={(e) => {setCredentials({... credentials, password: e.target.value})}}
              onKeyPress={handleKeyPress} // Added event handler for Enter key press
            />
          </CardBody>
          <div>
          <CardFooter className="pt-0">
            <Button  className="inline-flex items-center gap-2" variant="gradient" fullWidth onClick={loginForm}>
            Login <ArrowLongRightIcon strokeWidth={2} className="h-5 w-5" />
            </Button>
          </CardFooter>
          </div>
        </Card>
      </div>
    </>
  )
}
